body{
  margin: 0;
}
.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

.header .input-container{
  display: flex;
  flex-direction: column;
}
.header input{
  width: 200px;
  height: 40px;
  border: none;
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
  padding-left: 10px;
  border: 1px solid gray;
  font-size:17px
}
.header button{
  width: 70px;
  height: 87px;
  border: none;
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
  padding-left: 10px;
  cursor: pointer;
}
.header {
  flex:30%;
  background-color: tomato;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.todoList{
  flex: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}
.task { 
  width: 500px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}
.task .content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task .content span {
  display: grid;
  place-items: center;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: tomato;
}
.active { 
  border: none;
  margin-top: 2rem;
  border-radius: 6px;
  width: 8.5rem;
  height: 4rem;
  background-color: #fab845;
  font-size: 24px;
  color: black ;
}

.task button { 
  flex: 20%;
  height: 100%;
  border:none;
  background-color: rgb(32, 178, 170, 0.89);
  font-size: 16px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  color: white;
  cursor: pointer;
  transition: all ease-in-out 0.2s;
}
.task button:hover{
  background-color: rgb(32, 178, 170, 1);
}
